  --color-white-2: rgba(255, 77, 0, 0);
  --color-light-grey-2: #EDEDED;
  --color-bg-gray: #FAFAFA;
  --weight-300: 300;
  --weight-400: 400;
  --weight-500: 500;
  --weight-600: 600;
  --weight-700: 700;
  --weight-800: 800;
  --weight-900: 900;
  --font-h1:3rem;
  --font-h2:2rem;
  --font-label: 0.875rem;
