.gallery .card-pic .content-details {
  position: absolute;
  text-align: center;
  width: 100%;
  top: 50%;
  left: 50%;
  opacity: 0;
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  -webkit-transition: all 0.3s ease-in-out 0s;
  -moz-transition: all 0.3s ease-in-out 0s;
  transition: all 0.3s ease-in-out 0s;
}
.gallery .card-pic .content-details h3 {
  color: var(--color-white);
  font-weight: var(--weight-700);
  text-transform: capitalize;
  text-align: center;
}
@media (min-width: 0.1em) {
  .gallery .card-pic .content-details h3 {
    font-size: 1.1rem;
  }
}
@media (min-width: 62em) {
  .gallery .card-pic .content-details h3 {
    font-size: 1.8rem;
  }
}
.gallery .card-pic .fadeIn-top {
  top: 20%;
}
.gallery .card-pic:hover .img-overlay {
  opacity: 1;
}
.gallery .card-pic:hover .content-details {
  top: 50%;
  left: 50%;
  opacity: 1;
}

.order {
  margin-top: 1.9375rem;
}
.order-card {
  padding: 1rem 1.5rem;
  border: 1px solid var(--color-light-grey-2);
}
.order-link:hover .order-card, .order-link :active .order-card {
  border: 1px solid var(--color-orange);
  transition: all ease-in-out 0.2s;
}
.order-number {
  font-size: 1rem;
  font-weight: var(--weight-700);
}
.order-time {
  font-size: 0.875rem;
  font-weight: var(--weight-500);
}